:root {
    --header-height: 3rem;
    --nav-width: 190px;
    --first-color: #4723D9;
    --first-color-light: #AFA5D9;
    --white-color: #F7F6FB;
    --digi-green-icon : #009468;
    --body-font: 'Nunito', sans-serif;
    --normal-font-size: 1rem;
    --z-fixed: 100
}

*,
::before,
::after {
    box-sizing: border-box
}

body {
    position: relative;
    margin: 3rem 0 0 0;
    margin: var(--header-height) 0 0 0;
    padding: 0 1rem;
    font-family: 'Nunito', sans-serif;
    font-family: var(--body-font);
    font-size: 1rem;
    font-size: var(--normal-font-size);
    transition: .5s
}

a {
    text-decoration: none
}

.header {
    width: 100%;
    height: 3rem;
    height: var(--header-height);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    background-color: #F7F6FB;
    background-color: var(--white-color);
    z-index: 100;
    z-index: var(--z-fixed);
    transition: .5s
}

.header_toggle {
    color: #009468;
    color: var(--digi-green-icon);
    font-size: 1.5rem;
    cursor: pointer
}

.header_img {
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    overflow: hidden
}

.header_img img {
    width: 40px
}

.l-navbar {
    position: fixed;
    top: 0;
    left: -30%;
    width: 190px;
    width: var(--nav-width);
    height: 100vh;
    background-color: #4723D9;
    background-color: var(--first-color);
    padding: .5rem 1rem 0 0;
    transition: .5s;
    z-index: 100;
    z-index: var(--z-fixed);
    background-color:  rgb(206, 245, 234);
}

.nav {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden
}

.nav_logo,
.nav_link {
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    padding: .5rem 0 .5rem 1.5rem
}

.nav_logo {
    margin-bottom: 2rem
}

.nav_logo-icon {
    font-size: 1.25rem;
    color: #009468;
    color: var(--digi-green-icon)
}

.nav_logo-name {
    color: #009468;
    color: var(--digi-green-icon);
    font-weight: 700
}

.nav_link {
    position: relative;
    color: #009468;
    color: var(--digi-green-icon);
    margin-bottom: 1.5rem;
    transition: .3s
}

.nav_link:hover {
    color: #009468;
    color: var(--digi-green-icon)
}

.nav_icon {
    font-size: 1.25rem
}

.show {
    left: 0
}

.body-pd {
    padding-left: calc(190px + 1rem);
    padding-left: calc(var(--nav-width) + 1rem)
}

.active {
    color: #F7F6FB;
    color: var(--white-color)
}

.active::before {
    content: '';
    position: absolute;
    left: 0;
    width: 2px;
    height: 32px;
    background-color: #F7F6FB;
    background-color: var(--white-color)
}

.height-100 {
    height: 100vh
}

@media screen and (min-width: 768px) {
    body {
        margin: calc(3rem + 1rem) 0 0 0;
        margin: calc(var(--header-height) + 1rem) 0 0 0;
        padding-left: calc(190px + 2rem);
        padding-left: calc(var(--nav-width) + 2rem)
    }

    .header {
        height: calc(3rem + 1rem);
        height: calc(var(--header-height) + 1rem);
        padding: 0 2rem 0 calc(190px + 2rem);
        padding: 0 2rem 0 calc(var(--nav-width) + 2rem)
    }

    .header_img {
        width: 40px;
        height: 40px
    }

    .header_img img {
        width: 45px
    }

    .l-navbar {
        left: 0;
        padding: 1rem 1rem 0 0
    }

    .show {
        width: calc(190px + 156px);
        width: calc(var(--nav-width) + 156px)
    }

    .body-pd {
        padding-left: calc(190px + 188px);
        padding-left: calc(var(--nav-width) + 188px)
    }
}
